import React from 'react';
import './mainHeader.scss';

const MainHeader = React.memo((props) => {
    return (
    <div className={'main_button'}>
        <div className="header">
            {props.h === 'h1' ? 
            <h1>
                {props.title}
            </h1> :
            <h3>
                {props.title}
            </h3>
            }
        </div>
    </div>
    )
});

export default MainHeader;